.button {
  color: #ffffff;
  background-color: #9da5d2;
  padding: 5px;
  margin: 5px;
}

.user {
  display: flex;
  padding: 5px;
  background-color: #c0c3d7;
  margin-bottom: 5px;
}
.user:last-child {
  margin-bottom: 0;
}
.user .image {
  width: 70px;
  height: 70px;
}
.user .image img {
  max-width: 100%;
  height: auto;
  border-radius: 50%;
}
.user .description {
  padding: 5px;
}

