@import 'nullstyle.css';
.app-wrapper {
  position: relative;
  min-height: calc(100vh - 10px);
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-template-areas: "h h" "n c";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-gap: 5px;
}

.app-wrapper-content {
  position: relative;
  height: 100%;
  grid-area: c;
  background-color: #ececec;
}

#root {
  height: 100%;
}


